import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


const routes: Routes = [
  {
    path: 'company',
    loadChildren: () => import('./company/company.module').then(m => m.CompanyModule)
  },
  {
    path: 'retailer-company',
    loadChildren: () => import('./retailer-company/retailer-company.module').then(m => m.RetailerCompanyModule)
  },
  {
    path: 'retailer',
    loadChildren: () => import('./retailer/retailer.module').then(m => m.RetailerModule)
  },
  {
    path: 'financial',
    loadChildren: () => import ('./fin-ins/fin-ins.module'). then (m => m.FinInsModule)
  },
  {
    path: 'transporter',
    loadChildren: () => import ('./transporter/transporter.module'). then (m => m.TransporterModule)
  }
  // },
  // {
  //   path: 'truck',
  //   loadChildren: () => import('./truck/truck.module').then(m => m.TruckModule)
  // }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class EntitiesRoutingModule { }
